.page {
  margin: 0 auto;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  color: #3d3d3d;
  background-color: #f8f8f8;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /*overflow-x: hidden;*/
  max-width: 100%;
  width: 100%;
  max-height: 100%;
  height: 100%;
}

* {
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

a, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0;
}

ul, li {
  padding: 0;
  margin: 0;
}/*# sourceMappingURL=page.css.map */